<template>
  <div id="app">
    <div>
    <Particles v-if="isReady" v-bind:properties=getParticlesProps() 
               v-bind:parameters=getParticlesParams() ref="particles">
    </Particles>
    </div>

    <div v-bind:class="{ hidden: !menuIsShowing }" class="popup-holder" ref="pholder">
      <div class="hide-menu-button" v-on:click="clickHideMenu()"><i class="material-icons standard-padding">close</i></div>
      <div class="mini-heading">EPIDEMIC SIMULATOR</div>
      <div v-if="menuItem===0">
        <h2>Controls</h2>
        <div v-if="isReady">
          <Slider v-for="params in getSliderParams()" v-bind:key="params.key" 
              v-bind:parameters="params" v-bind:properties="getSliderProps(params)"></Slider>
        </div>
      </div>
      <div v-if="menuItem===1">
        <h2>About</h2>
        <p>Blahblahblah.</p>
      </div>
      <div v-if="menuItem===2">
        <h2>"The Curve"</h2>
        <p>Blahblahblah.</p>
        <p>Blahblahblah.</p>
      </div>
    </div>
    <div>

    </div>
    <div class="reset-button"> 
      <div v-on:click="clickReset()"><i class="material-icons standard-padding">refresh</i></div>
    </div>
    
    <div v-if="!menuIsShowing" class="show-menu-button">
      <div v-on:click="clickShowMenu(0)"><i class="material-icons standard-padding">settings</i></div>
      <div v-on:click="clickShowMenu(1)"><i class="material-icons standard-padding">info</i></div>
      <div v-on:click="clickShowMenu(2)"><i class="material-icons standard-padding">timeline</i></div>
    </div>
  </div>
</template>

<script>
import Particles from './components/Particles.vue';
import Slider from './components/Slider.vue';
import paper from 'paper';

export default {
  name: 'App',
  components: {
    Particles, Slider
  },
  data: function(){
    return {
      isReady: false,
      params: this.initializeParams(),
      resetTrigger: false,
      menuIsShowing: false
    }
  },
  methods: {
    initializeParams() {
      //This is necessary because we want to create a new 'resetValue' variable based on 'value'.
      let params = [
        {'key':'average_speed', 'title': 'Average Speed', 'value': 0.5, 'min': 0, 'max': 2 },
        {'key':'speed_range', 'title': 'Speed Range', 'value': 0.5, 'min': 0, 'max': 1.0 },
        {'key':'transmission_distance', 'title': 'Transmission Distance', 'value': 5, 'min': 0, 'max': 20 },
        {'key':'transmission_probability', 'title': 'Transmission Probability', 'value': 0.1, 'min': 0, 'max': 1.0 }
      ]
      params.forEach(param => {
        param.resetValue = param.value;
      });
      return params;
    },
    getParticlesProps(){
      return {
        id: 'particles',
        width: 0.99 * window.innerWidth,
        height: 0.99 * window.innerHeight,
        isReady: this.isReady
      }
    },
    getSliderProps(params){
      let props = Object.assign(params, {
        id: params.key + '-slider',
        width: this.sliderWidth * 0.75,
        height: this.sliderWidth * 0.15,
        weights: {
          paddingTop: 2,
          paddingBottom: 0,
          titleFontSize: 5,
          paddingMiddle: 2,
          slotWidth: 6
        },
      });
      return props;
    },
    getSliderParams(){
      return this.params;
    },
    getParticlesParams(){
      let particlesParams = {}
      this.params.forEach(param => {
        particlesParams[param.key] = param;
      });
      return particlesParams;
    },
    clickReset(){
      this.$refs.particles.triggerReset();
    },
    clickShowMenu(menuItem){
      this.menuIsShowing = true;
      this.menuItem = menuItem;
    },
    clickHideMenu(){
      this.menuIsShowing = false;
    }
  },
  mounted: function(){
    paper.install(window);
    this.isReady = true;
    this.sliderWidth = this.$refs.pholder.offsetWidth;
    this.sliderHeight = this.$refs.pholder.offsetHeight;
  }
}
</script>

